.home_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;

}

.home_container h1 {
    font-size: 2.5em;
    margin-bottom: .5em;
}

.home_container h1 span {
    color: #ffdd20;
    padding: 0.1em 0.2em;
    background-color: rgba(2, 19, 35, 1);
    border-radius: 20px;
}

.home_container p {
    margin-bottom: 1.5em;
    color: rgba(2, 19, 35, 1);
}

.home_container img {
    width: 350px;
    margin: 2;
}